@import url(//netdna.bootstrapcdn.com/font-awesome/4.0.3/css/font-awesome.css);

@import url(http://fonts.googleapis.com/css?family=Titillium+Web:300);
.fa-2x {
font-size: 2em;
}
.fa {
position: relative;
display: table-cell;
width: 60px;
height: 36px;
text-align: center;
vertical-align: middle;
font-size:20px;
}


.main-menu:hover,nav.main-menu.expanded {
width:250px;
overflow:visible;
}

.main-menu{
background:#1e4080;
border-right:1px solid #e5e5e5;
position:fixed;
top:0;
bottom:0;
height:100%;
left:0;
width:60px;
overflow:hidden;
-webkit-transition:width .05s ease;
transition:width .05s ease;
/*-webkit-transform:translateZ(0) scale(1,1);*/
z-index:1000;
}

.main-menu>ul {
margin:7px 0;
}

.main-menu li {
position:relative;
display:block;
width:250px;
padding-right: 8%;
}

.main-menu li>a {
position:relative;
display:table;
border-collapse:collapse;
border-spacing:0;
color:rgb(250, 242, 242);
 font-family: arial;
font-size: 14px;
text-decoration:none;
/*-webkit-transform:translateZ(0) scale(1,1);*/
-webkit-transition:all .1s ease;
transition:all .1s ease;
  
}

.main-menu .nav-icon {
position:relative;
display:table-cell;
width:60px;
height:36px;
text-align:center;
vertical-align:middle;
font-size:18px;
}

.main-menu .nav-text {
position:relative;
display:table-cell;
vertical-align:middle;
width:190px;
font-family: 'Titillium Web', sans-serif;
}
.main-menu>ul.logout {
position:absolute;
left:0;
bottom:0;
}

.no-touch .scrollable.hover {
overflow-y:hidden;
}

.no-touch .scrollable.hover:hover {
overflow-y:auto;
overflow:visible;
}

a:hover,a:focus {
text-decoration:none;
}

nav {
-webkit-user-select:none;
-moz-user-select:none;
-ms-user-select:none;
-o-user-select:none;
user-select:none;
}

nav ul,nav li {
outline:0;
margin:0;
padding:0;
}
.main-menu li:hover>a,nav.main-menu li.active>a,.dropdown-menu>li>a:hover,.dropdown-menu>li>a:focus,
.dropdown-menu>.active>a,.dropdown-menu>.active>a:hover,.dropdown-menu>.active>a:focus,
.no-touch .dashboard-page nav.dashboard-menu ul li:hover a,
.dashboard-page nav.dashboard-menu ul li.active a {
color:#fff;
background-color:var(--hover-color);

}
.area {
float: left;
background: var(--hover-color);
width: 100%;
height: 100%;
}
@font-face {
  font-family: 'Titillium Web';
  font-style: normal;
  font-weight: 300;
  src: local('Titillium WebLight'), local('TitilliumWeb-Light'), url(http://themes.googleusercontent.com/static/fonts/titilliumweb/v2/anMUvcNT0H1YN4FII8wpr24bNCNEoFTpS2BTjF6FB5E.woff) format('woff');
}

.UnList{
  color:#fff;
  background-color:var(--hover-color);
}
.img-avatar {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid rgb(222, 228, 238);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  display: block;
  align-content: flex-end;
  margin-left: 8px;
  margin-right: 15px; 
  line-height: 70px;
  user-select: none;
}
.main-menu:focus {
  width: 250px;
}
.main-menu:hover .img-avatar{
  width: 100px;
  height: 100px;
  border: 3px solid rgb(222, 228, 238);
  margin-left: 50px;
  margin-right: 15px; 
  transition: all .3s ease;
}
.highlight-font:hover{
  padding-left: 2px;

}
.detail-sec{
  padding-left: 8%;
  margin-top: 0px;
  background-color: #e6e8ea;
  height: 97vh;

  

}
.main-menu:hover ~ section{
  padding-left: 20%;
}
